import { AutoEditContext, EditingEverythingContext } from "./App";
import { useCallback, useContext } from "react";

import { insertNewProject } from "./database";

export function NewProjectButton({ show }) {
  const { setEditingEverything } = useContext(EditingEverythingContext);
  const { setAutoEdit } = useContext(AutoEditContext);

  const focus = useCallback(() => {
    const input = document.createElement("input");
    document.body.appendChild(input);
    input.style.position = "absolute";
    input.style.top = "-100px";
    input.style.width = "0px";
    input.style.height = "0px";
    input.type = "text";
    input.focus();
    setTimeout(() => {
      document.body.removeChild(input);
    }, 100);
  }, []);

  return (
    <div
      className={[
        "new-project-button",
        show && "new-project-button-reveal",
      ].join(" ")}
      onClick={(e) => {
        const projectId = insertNewProject();
        focus();
        setAutoEdit(projectId);
        setEditingEverything(false);
        e.preventDefault();
        e.stopPropagation();
      }}
    >
      + New Project
    </div>
  );
}
